import {Voxel} from './voxel';
import {BufferGeometry, Color, LineBasicMaterial, LineDashedMaterial, LineSegments, Material, Vector3} from "three";
import {Point} from "./point";
import Tracer, {Direction, Path, PathStep} from "./tracer";
import SceneNode from "./scene-node";
import {MAXIMUM_HEALTH} from "./health";
import {randomInt} from "./utils";
import {Team} from "./team";

export class Lattice extends SceneNode {
    readonly length: number;
    readonly voxels: Voxel[][][];
    readonly guidelines: Guidelines;
    readonly tracers: Tracer[] = new Array<Tracer>();

    // TODO: Remove.
    private tracerSpawnThreshold: number = 300;
    private tracerSpawnCounter: number = 0;

    constructor(
        readonly size: number,
        readonly voxelSize: number,
        readonly spacing: number
    ) {
        super();

        this.length = (this.size - 1) * this.spacing;

        this.voxels = new Array<Array<Array<Voxel>>>(this.size);
        for (let x = 0; x < this.size; x++) {
            this.voxels[x] = new Array<Array<Voxel>>(this.size);
            for (let y = 0; y < this.size; y++) {
                this.voxels[x][y] = new Array<Voxel>(this.size);
            }
        }

        for (let x = 0; x < this.size; x++) {
            for (let y = 0; y < this.size; y++) {
                for (let z = 0; z < this.size; z++) {
                    const voxel = this.voxels[x][y][z] = new Voxel(
                        voxelSize,
                        new Color('#111111'),
                        5,
                        new Color('#ffffff'),
                        1.05,
                        6
                    );

                    voxel.group.position.set(
                        x * this.spacing - this.length / 2,
                        y * this.spacing - this.length / 2,
                        z * this.spacing - this.length / 2
                    );
                    this.group.add(voxel.group);
                }
            }
        }

        this.guidelines = new Guidelines(this.length, this.voxelSize, this.spacing);
        this.group.add(this.guidelines.segments);
    }

    addTracer(tracer: Tracer) {
        this.tracers.push(tracer);
        this.group.add(tracer.group);
    }

    update(delta: number) {
        if (this.tracerSpawnCounter++ > this.tracerSpawnThreshold) {
            const team = Team.fromID(randomInt(0, Team.count()))!;
            const health = randomInt(1, MAXIMUM_HEALTH);

            const steps = new Array<PathStep>();
            const start = new Vector3(0, 0, 0);
            const curr = new Vector3(0, 0, 0);

            for (let i = 0; i < 32; i++) {
                let direction = Direction.UP;
                switch (randomInt(0, 6)) {
                    case 0:
                        direction = Direction.UP;
                        break;
                    case 1:
                        direction = Direction.DOWN;
                        break;
                    case 2:
                        direction = Direction.LEFT;
                        break;
                    case 3:
                        direction = Direction.RIGHT;
                        break;
                    case 4:
                        direction = Direction.FORWARD;
                        break;
                    case 5:
                        direction = Direction.BACK;
                        break;
                }

                const v = new Vector3().copy(direction.vector).multiplyScalar(this.spacing);
                const dest = new Vector3().copy(curr).add(v)

                if (dest.x < -this.length / 2 || dest.x > +this.length / 2 ||
                    dest.y < -this.length / 2 || dest.y > +this.length / 2 ||
                    dest.z < -this.length / 2 || dest.z > +this.length / 2) {
                    continue;
                }

                steps.push(new PathStep(direction, this.spacing, 1500));
                curr.copy(dest);
            }

            const tracer = new Tracer(
                team,
                health,
                start,
                new Path(steps),
            );
            this.addTracer(tracer);

            this.tracerSpawnThreshold = Math.random() * 64;
            this.tracerSpawnCounter = 0;
        }

        const completeTracers: Array<number> = [];
        for (let i = 0; i < this.tracers.length; i++) {
            const tracer = this.tracers[i];

            tracer.update(delta);

            if (tracer.hasReachedVoxel()) {
                const voxel = this.getVoxelFromWorldCoordinates(tracer.position);
                const overflow = voxel.damage(tracer.health, tracer.shellMaterial.color);
                tracer.damage(overflow);
            }

            if (tracer.dead()) {
                this.group.remove(tracer.group);
                completeTracers.push(i);
            }
        }

        for (let i = completeTracers.length - 1; i >= 0; i--) {
            this.tracers.splice(completeTracers[i], 1);
        }
    }

    getVoxelFromWorldCoordinates({x, y, z}: Vector3): Voxel {
        return this.getVoxel({
            x: (x + this.length / 2) / this.spacing,
            y: (y + this.length / 2) / this.spacing,
            z: (z + this.length / 2) / this.spacing
        });
    }

    /**
     *
     * @param x
     * @param y
     * @param z
     */
    getVoxel({x, y, z}: Point): Voxel {
        return this.voxels[x][y][z];
    }
}

export class Guidelines extends SceneNode {
    readonly material: Material;
    readonly segments: LineSegments;

    constructor(length: number, voxelSize: number, spacing: number) {
        super();

        this.material = new LineBasicMaterial({
            color: new Color('#ffffff'),
        });

        const halfLength = length / 2;

        const points = new Array<Vector3>();
        for (let a = -halfLength; a <= halfLength; a += spacing) {
            for (let b = -halfLength; b <= halfLength; b += spacing) {
                for (let c = -halfLength; c < halfLength; c += spacing) {
                    const gapStart = c + voxelSize / 2;
                    const gapEnd = c + spacing - voxelSize / 2;

                    const gapLength = gapEnd - gapStart;
                    const len = gapLength * 0.10;

                    points.push(new Vector3(
                        a,
                        b,
                        gapStart,
                    ));

                    points.push(new Vector3(
                        a,
                        b,
                        gapStart + len,
                    ));

                    points.push(new Vector3(
                        a,
                        b,
                        gapEnd - len,
                    ));

                    points.push(new Vector3(
                        a,
                        b,
                        gapEnd,
                    ));


                    points.push(new Vector3(
                        a,
                        gapStart,
                        b,
                    ));

                    points.push(new Vector3(
                        a,
                        gapStart + len,
                        b,
                    ));

                    points.push(new Vector3(
                        a,
                        gapEnd - len,
                        b,
                    ));

                    points.push(new Vector3(
                        a,
                        gapEnd,
                        b,
                    ));


                    points.push(new Vector3(
                        gapStart,
                        a,
                        b,
                    ));

                    points.push(new Vector3(
                        gapStart + len,
                        a,
                        b,
                    ));

                    points.push(new Vector3(
                        gapEnd - len,
                        a,
                        b,
                    ));

                    points.push(new Vector3(
                        gapEnd,
                        a,
                        b,
                    ));
                }
            }
        }

        this.segments = new LineSegments(new BufferGeometry().setFromPoints(points), this.material);
        this.segments.computeLineDistances();
        this.group.add(this.segments);
    }
}