import {
    BackSide,
    BoxGeometry,
    Color, DataTexture,
    EdgesGeometry, Geometry, Group,
    LineBasicMaterial,
    LineSegments,
    LuminanceFormat, Material,
    Mesh, MeshPhongMaterial,
    MeshToonMaterial,
    NearestFilter, OctahedronGeometry,
    Texture
} from 'three';

import {
    getCachedSphereGeometry,
    getCachedGradientMap,
    getCachedBoxGeometry
} from "./geometry-cache";

import SceneNode from "./scene-node";

/**
 * A voxel is a
 */
export class Voxel extends SceneNode {
    readonly shellGeometry: Geometry;
    readonly shellMaterial: MeshToonMaterial;
    readonly shellMesh: Mesh;

    readonly highlightGeometry: Geometry;
    readonly highlightMaterial: MeshToonMaterial;
    readonly highlightMesh: Mesh;

    readonly dotGeometry: Geometry;
    readonly dotMaterial: MeshToonMaterial;
    readonly dotMesh: Mesh;

    health: number = 0;

    constructor(
        size: number,
        color: Color,
        gradientTones: number,
        highlightColor: Color,
        highlightScalar: number,
        dotRadius: number,
    ) {
        super();

        this.shellGeometry = getCachedBoxGeometry(size);
        this.shellMaterial = new MeshToonMaterial({
            color: color,
            gradientMap: getCachedGradientMap(gradientTones),
            side: BackSide
        });
        this.shellMesh = new Mesh(this.shellGeometry, this.shellMaterial);
        this.shellMesh.renderOrder = 1;
        this.group.add(this.shellMesh);

        this.highlightGeometry = getCachedBoxGeometry(size);
        this.highlightMaterial = new MeshToonMaterial({
            color: highlightColor,
            gradientMap: getCachedGradientMap(gradientTones),
            side: BackSide
        });
        this.highlightMesh = new Mesh(this.highlightGeometry, this.highlightMaterial);
        this.highlightMesh.scale.multiplyScalar(highlightScalar);
        this.highlightMesh.renderOrder = 0;
        this.group.add(this.highlightMesh);

        this.dotGeometry = getCachedSphereGeometry(dotRadius);
        this.dotMaterial = new MeshToonMaterial({
            color: new Color('#ff0000'),
            gradientMap: getCachedGradientMap(gradientTones),
        });
        this.dotMesh = new Mesh(this.dotGeometry, this.dotMaterial);
        this.dotMesh.scale.multiplyScalar(1.1);
        this.dotMesh.visible = false;
        this.dotMesh.renderOrder = 2;
        this.group.add(this.dotMesh);
    }

    damage(amount: number, color: Color): number {
        if (this.dotMaterial.color.equals(color)) {
            return 0;
        }

        let overflow: number = 0;

        if (this.health === 0) {
            if (amount === 1) {
                this.dotMaterial.color = new Color().copy(color);
                this.dotMesh.visible = true;
                this.health = 1;
                overflow = 1;
            } else {
                this.dotMaterial.color = new Color().copy(color);
                this.dotMesh.visible = true;
                this.health = Math.floor(amount / 2);
                overflow = amount - this.health;
            }
        } else if (this.health - amount < 0) {
            this.dotMaterial.color = new Color().copy(color);
            this.dotMesh.visible = true;
            this.health = Math.floor((amount - this.health) / 2);
            overflow = amount - this.health;
        } else if(this.health - amount === 0) {
            this.dotMaterial.color = new Color().copy(this.shellMaterial.color);
            this.dotMesh.visible = false;
            this.health = 0;
            overflow = this.health;
        } else {
            this.health -= amount;
            overflow = amount;
        }

        const scale = Math.floor((this.health + 100) / 100) / 6;
        this.dotMesh.scale.set(1, 1, 1).multiplyScalar(scale).multiplyScalar(1.1);

        return overflow;
    }
}