import {AmbientLight, Color, PerspectiveCamera, PointLight, Scene, WebGLRenderer} from "three";
import {Lattice} from "./lattice";
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls";
import {EffectComposer} from "three/examples/jsm/postprocessing/EffectComposer";
import {RenderPass} from "three/examples/jsm/postprocessing/RenderPass";

export default class Game {
    private renderer: WebGLRenderer;
    private effects: EffectComposer;
    private camera: PerspectiveCamera;
    private controls: OrbitControls;
    private scene: Scene;
    private ambient: AmbientLight;
    private sun: PointLight;
    private lattice: Lattice;

    constructor(private canvas: HTMLCanvasElement) {
        this.renderer = new WebGLRenderer({
            canvas: this.canvas,
            antialias: true,
        });
        this.renderer.setSize(canvas.clientWidth, canvas.clientHeight);

        this.camera = new PerspectiveCamera(45, canvas.clientWidth / canvas.clientHeight, 1, 8000);
        this.controls = new OrbitControls(this.camera, this.canvas);

        this.controls.maxPolarAngle = Math.PI / 2;

        this.controls.dampingFactor = 0.025;
        this.controls.enableDamping = true;

        this.camera.position.set(400, 400, 400);
        this.controls.update();

        this.scene = new Scene();
        this.scene.background = new Color('#000000');

        this.ambient = new AmbientLight('#ffffff', 1.0);
        this.scene.add(this.ambient);

        this.sun = new PointLight('#ffffff', 0.5);
        this.sun.position.set(800, 800, 800);
        this.scene.add(this.sun);

        this.lattice = new Lattice(9, 16, 100);

        this.scene.add(this.lattice.group);

        this.effects = new EffectComposer(this.renderer);
        this.effects.addPass(new RenderPass(this.scene, this.camera));
    }

    draw(delta: number) {
        this.lattice.update(delta);

        // TODO(271): Only call this when the element has been resized.
        this.camera.aspect = this.canvas.clientWidth / this.canvas.clientHeight;
        this.camera.updateProjectionMatrix();

        // TODO(271): Only call this when the element has been resized.
        this.renderer.setSize(this.canvas.clientWidth, this.canvas.clientHeight);

        this.controls.update();
        this.effects.render();
    }
}