import {
    BackSide,
    Color, DataTexture, FrontSide, Geometry,
    Group, LuminanceFormat, Material, MathUtils,
    Mesh,
    MeshBasicMaterial,
    MeshPhongMaterial, MeshToonMaterial, NearestFilter, OctahedronGeometry,
    PointLight,
    SphereGeometry,
    Vector3
} from "three";
import {getCachedGradientMap, getCachedSphereGeometry} from "./geometry-cache";
import SceneNode from "./scene-node";
import {healthToScalar} from "./health";
import {Dir} from "fs";
import {Team} from "./team";
import {darkenColor} from "./utils";

const RADIUS: number = 6;

export default class Tracer extends SceneNode {

    readonly shellGeometry: Geometry;
    readonly shellMaterial: MeshToonMaterial;
    readonly shellMesh: Mesh;

    readonly outlineGeometry: Geometry;
    readonly outlineMaterial: MeshPhongMaterial;
    readonly outlineMesh: Mesh;

    private alpha: number = 0;
    private stepTime: number = 0;
    private stepSource: Vector3 = new Vector3();
    private easing: (t: number) => number = sigmoid(5.5);

    /**
     *
     * @param team
     * @param start
     * @param path
     * @param health
     */
    constructor(
        readonly team: Team,
        public health: number,
        readonly start: Vector3,
        readonly path: Path,
    ) {
        super();

        this.shellGeometry = getCachedSphereGeometry(RADIUS);
        this.shellMaterial = new MeshToonMaterial({
            color: team.color,
            gradientMap: getCachedGradientMap(10),
            side: FrontSide
        });
        this.shellMesh = new Mesh(this.shellGeometry, this.shellMaterial);
        this.shellMesh.renderOrder = 1;
        this.group.add(this.shellMesh);

        this.outlineGeometry = getCachedSphereGeometry(RADIUS);
        this.outlineMaterial = new MeshPhongMaterial({
            color: darkenColor(this.team.color, 0.2),
            side: BackSide
        });
        this.outlineMesh = new Mesh(this.outlineGeometry, this.outlineMaterial);
        this.outlineMesh.scale.multiplyScalar(1.05);
        this.outlineMesh.renderOrder = 0;
        this.group.add(this.outlineMesh);

        this.position.copy(this.stepSource);
        this.scale.multiplyScalar(healthToScalar(this.health));

        this.stepSource.copy(this.start);
    }

    /**
     * Damages the tracer, updating its scale as needed.
     *
     * @param amount the amount of damage to apply to the tracer.
     */
    damage(amount: number) {
        this.health -= amount;
        if (this.health < 0) {
            this.health = 0;
        }

        if (this.health !== 0) {
            this.group.scale.set(1, 1, 1).multiplyScalar(healthToScalar(this.health));
        }
    }

    update(delta: number) {
        if (this.path.complete()) {
            return;
        }

        const step = this.path.peek();
        const dest = new Vector3().addVectors(
            this.stepSource,
            step.delta(),
        );

        this.stepTime += delta;

        this.alpha = this.easing(this.stepTime / step.time);
        if (this.alpha > 1) {
            this.alpha = 1;
        }

        this.position.copy(new Vector3().lerpVectors(this.stepSource, dest, this.alpha));

        if (this.alpha === 1.0) {
            this.path.poll();

            this.stepSource = dest;
            this.alpha = this.stepTime = 0;
        }
    }

    hasReachedVoxel(): boolean {
        return this.alpha === 0;
    }

    /**
     * Gets if the tracer is dead either because it has zero health or it reached the end of its path.
     */
    dead() : boolean {
        return this.health === 0 || this.path.complete();
    }
}

export class Path {
    private index = 0;

    constructor(readonly steps: PathStep[]) {}

    peek(): PathStep {
        return this.steps[this.index];
    }

    poll(): PathStep {
        return this.steps[this.index++];
    }

    complete(): boolean {
        return this.index === this.steps.length;
    }
}

export class PathStep {

    /**
     *
     * @param direction
     * @param distance
     * @param time the time in milliseconds that the step should be taken.
     */
    constructor(readonly direction: Direction, readonly distance: number, readonly time: number) {
    }

    delta(): Vector3 {
        return new Vector3().copy(this.direction.vector).multiplyScalar(this.distance);
    }
}

export class Direction {
    public static UP = new Direction(new Vector3(0, 1, 0));
    public static DOWN = new Direction(new Vector3(0, -1, 0));
    public static LEFT = new Direction(new Vector3(1, 0, 0));
    public static RIGHT = new Direction(new Vector3(-1, 0, 0));
    public static FORWARD = new Direction(new Vector3(0, 0, 1));
    public static BACK = new Direction(new Vector3(0, 0, -1));

    private constructor(readonly vector: Vector3) {}
}

/**
 * Factory for sigmoid functions with a specific k value. For more information about sigmoid functions go here:
 * https://en.wikipedia.org/wiki/Sigmoid_function.
 *
 * @param k the sigmoid k value.
 */
const sigmoid = (k: number) => {
    const base = (t: number) => {
        return (1 / (1 + Math.exp(-k * t))) - 0.5;
    }

    const correction = 0.5 / base(1);

    return (t: number): number => {
        t = MathUtils.clamp(t, 0, 1);
        return correction * base(2 * t - 1) + 0.5;
    }
}