import {Color} from "three";

export class Team {
    private static ALL = new Map<number, Team>();

    public static TEAM_A = new Team(0, new Color().setHSL(0.0, 0.5, 0.5));
    public static TEAM_B = new Team(1, new Color().setHSL(0.4, 0.5, 0.5));
    public static TEAM_C = new Team(2, new Color().setHSL(0.6, 0.5, 0.5));
    public static TEAM_D = new Team(3, new Color().setHSL(0.8, 0.5, 0.5));

    private constructor(readonly id: number, readonly color: Color) {
        Team.ALL.set(id, this);
    }

    public static fromID(id: number): Team | undefined {
        return Team.ALL.get(id);
    }

    public static count(): number {
        return Team.ALL.size;
    }
}