import {
    BoxGeometry,
    DataTexture,
    EdgesGeometry,
    Geometry,
    LuminanceFormat,
    NearestFilter,
    SphereGeometry,
    Texture
} from "three";

const SPHERE_GEOMETRIES = new Map<number, Geometry>();

export const getCachedSphereGeometry = (radius: number): Geometry => {
    if (SPHERE_GEOMETRIES.has(radius)) {
        return SPHERE_GEOMETRIES.get(radius)!;
    }

    const geometry = new SphereGeometry(radius);
    SPHERE_GEOMETRIES.set(radius, geometry);
    return geometry;
}

const BOX_GEOMETRIES = new Map<number, Geometry>();

export const getCachedBoxGeometry = (size: number): Geometry => {
    if (BOX_GEOMETRIES.has(size)) {
        return BOX_GEOMETRIES.get(size)!;
    }

    const geometry = new BoxGeometry(size, size, size);
    BOX_GEOMETRIES.set(size, geometry);
    return geometry;
}

const EDGE_GEOMETRIES = new Map<number, EdgesGeometry>();

export const getCachedEdgesGeometry = (size: number): EdgesGeometry => {
    if (EDGE_GEOMETRIES.has(size)) {
        return EDGE_GEOMETRIES.get(size)!;
    }

    const geometry = new EdgesGeometry(getCachedBoxGeometry(size));
    EDGE_GEOMETRIES.set(size, geometry);
    return geometry;
}

const GRADIENT_MAPS = new Map<number, Texture>();

export const getCachedGradientMap = (tones: number): Texture => {
    if (GRADIENT_MAPS.has(tones)) {
        return GRADIENT_MAPS.get(tones)!;
    }

    const texels = new Uint8Array( tones);
    for (let i = 0; i <= texels.length; i ++ ) {
        texels[i] = (i / texels.length) * 0x100;
    }

    const texture = new DataTexture(texels, texels.length, 1, LuminanceFormat);
    texture.minFilter = NearestFilter;
    texture.magFilter = NearestFilter;
    texture.generateMipmaps = false;
    GRADIENT_MAPS.set(tones, texture);

    return texture;
}